import { Component, OnInit } from '@angular/core';
import { FormControl, ValidatorFn, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';


import * as _ from 'lodash';
import { Observable } from 'rxjs/Observable';

import { PoolService } from '../../../shared/api/pool.service';
import { TaskWrapperService } from '../../../shared/services/task-wrapper.service';
import { CdFormGroup } from '../../../shared/forms/cd-form-group';
import { FinishedTask } from '../../../shared/models/finished-task';
import { PoolFormMode } from './pool-form-mode.enum';
@Component({
  selector: 'cd-pool-form',
  templateUrl: './pool-form.component.html',
  styleUrls: ['./pool-form.component.scss']
})
export class PoolFormComponent implements OnInit {

  poolForm: CdFormGroup;
  poolTypes: String[] = ["replicated"];
  public poolFormMode = PoolFormMode;
  mode: PoolFormMode;

  constructor(
    private router: Router,
    private taskWrapper: TaskWrapperService,
    private poolService: PoolService,
  ) {
    this.createForm();
  }

  ngOnInit() {
    if (this.router.url.startsWith('/pool/add')) {
      this.mode = this.poolFormMode.createing;
    }
  }

  createForm() {
    this.poolForm = new CdFormGroup(
      {
        name: new FormControl('', {
          validators: [Validators.required]
        }),
        type: new FormControl(null, {
          validators: [Validators.required],
        }),
        pg_num: new FormControl(0),
      },
      this.validatePoolForm());
  }


  validatePoolForm(): ValidatorFn {
    return (formGroup: CdFormGroup) => {
      // PGs
      const pgsControl = formGroup.get('pg_num');
      let pgsControlErrors = null;
      if (pgsControl.value === null) {
        pgsControlErrors = { required: true };
      } else {
        if (pgsControl.value > 600 || pgsControl.value < 125) {
          pgsControlErrors = { invalidNm: true };
        }

      }
      pgsControl.setErrors(pgsControlErrors);
      return null;
    };
  }


  submit() {
    const request = {
      pool: this.poolForm.getValue('name'),
      pool_type: this.poolForm.getValue('type'),
      pg_num: this.poolForm.getValue('pg_num'),
    };
    let action: Observable<any>;
    action = this.taskWrapper.wrapTaskAroundCall({
      task: new FinishedTask('pool/add', {
        pool: request.pool,
        pool_type: request.pool_type,
        pg_num: request.pg_num,
      }),
      call: this.poolService.create(request)
    });
    action.subscribe(
      undefined,
      () => this.poolForm.setErrors({ cdSubmitButton: true }),
      () => this.router.navigate(['/pool'])
    );
  }
}
