import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { BsModalRef } from 'ngx-bootstrap';

import { OsdService } from '../../../../shared/api/osd.service';
import { NotificationType } from '../../../../shared/enum/notification-type.enum';
import { NotificationService } from '../../../../shared/services/notification.service';

@Component({
  selector: 'cd-osd-in-out',
  templateUrl: './osd-in-out-modal.component.html',
  styleUrls: ['./osd-in-out-modal.component.scss']
})
export class OsdInOutComponent implements OnInit {

  mode: string;
  inoutForm: FormGroup;
  selected = [];

  constructor(
    private osdService: OsdService,
    private notificationService: NotificationService,
    private bsModalRef: BsModalRef,
  ) {
    this.createForm();
  }

  ngOnInit() {
  }

  createForm() {
    this.inoutForm = new FormGroup({});
  }

  submit() {
    const id = this.selected[0].id;

    if (this.mode === 'in') {
      this.osdService.mark_in(id).subscribe((res) => {
        this.notificationService.show(
          NotificationType.success,
          `Osd ${id} was marked as 'in'`
        );

        this.bsModalRef.hide();
      }, () => {
        this.bsModalRef.hide();
      });
    }
    if (this.mode === "out") {
      this.osdService.mark_out(id).subscribe((res) => {
        this.notificationService.show(
          NotificationType.success,
          `Osd ${id} was marked as 'out'`
        );

        this.bsModalRef.hide();
      }, () => {
        this.bsModalRef.hide();
      });
    }
  }

}
