import { Component } from '@angular/core';

import { BsModalRef, BsModalService } from 'ngx-bootstrap';

import { PoolService } from '../../../shared/api/pool.service';
import { AuthStorageService } from '../../../shared/services/auth-storage.service';
import { TaskWrapperService } from '../../../shared/services/task-wrapper.service';
import { CdTableColumn } from '../../../shared/models/cd-table-column';
import { CdTableSelection } from '../../../shared/models/cd-table-selection';
import { Permission } from '../../../shared/models/permissions';
import { DeletionModalComponent } from '../../../shared/components/deletion-modal/deletion-modal.component';
import { ExecutingTask } from '../../../shared/models/executing-task';
import { FinishedTask } from '../../../shared/models/finished-task';

@Component({
  selector: 'cd-pool-list',
  templateUrl: './pool-list.component.html',
  styleUrls: ['./pool-list.component.scss']
})
export class PoolListComponent {
  pools = [];
  columns: CdTableColumn[];
  selection = new CdTableSelection();
  permission: Permission;
  modalRef: BsModalRef;
  executingTasks: ExecutingTask[] = [];

  constructor(
    private poolService: PoolService,
    private authStorageService: AuthStorageService,
    private modalService: BsModalService,
    private taskWrapper: TaskWrapperService,
  ) {
    this.columns = [
      {
        prop: 'pool_name',
        name: 'Name',
        flexGrow: 3
      },
      {
        prop: 'type',
        name: 'Type',
        flexGrow: 2
      },
      {
        prop: 'application_metadata',
        name: 'Applications',
        flexGrow: 3
      },
      {
        prop: 'pg_placement_num',
        name: 'Placement Groups',
        flexGrow: 1,
        cellClass: 'text-right'
      },
      {
        prop: 'size',
        name: 'Replica Size',
        flexGrow: 1,
        cellClass: 'text-right'
      },
      {
        prop: 'last_change',
        name: 'Last Change',
        flexGrow: 1,
        cellClass: 'text-right'
      },
      {
        prop: 'erasure_code_profile',
        name: 'Erasure Coded Profile',
        flexGrow: 2
      },
      {
        prop: 'crush_rule',
        name: 'Crush Ruleset',
        flexGrow: 2
      }
    ];
    this.permission = this.authStorageService.getPermissions().pool;
  }

  updateSelection(selection: CdTableSelection) {
    this.selection = selection;
  }

  getPoolList() {
    this.poolService.getList().subscribe((pools: any[]) => {
      this.pools = pools;
    });
  }

  deletePoolModal() {
    console.log(this.selection);
    const poolName = this.selection.first().pool_name;
    this.modalRef = this.modalService.show(DeletionModalComponent);
    this.modalRef.content.setUp({
      metaType: 'POOL',
      pattern: `${poolName}`,
      modalRef: this.modalRef,
      deletionObserver: () =>
        this.taskWrapper.wrapTaskAroundCall({
          task: new FinishedTask('pool/delete', { pool_name: poolName }),
          tasks: this.executingTasks,
          call: this.poolService.delete(poolName)
        }),
    });
  }
}
